import React from "react";
import "./style.scss";

const Sobre = () => {
  return (
    <div id="sobre" className="sobre_wrapper">
      <h2>Sobre mim</h2>
      <div className="sobre_image_container">
        <div className="sobre_image_card" />
      </div>
      <br />
      <div className="sobre_text">
        <h3>
          Natural do estado do Maranho, graduado pela Universidade Federal do
          Maranho (UFMA) no curso de Cincia da Computao, venho atuando no
          desenvolvimento web Front-end desde 2021.
        </h3>
        <h3>
          Utilizando como principais tecnologias <b>React</b>, <b>Angular</b>,{" "}
          <b>Javascript</b>, <b>Typescript</b>, alm de <b>HTML5</b> e{" "}
          <b>CSS3</b>, venho desenvolvendo aplicaes Web com foco em
          acessibilidade, praticidade e usabilidade para que todos os usurios
          possam ser includos e consigam utilizar as aplicaes de acordo com
          sua necessidade.
        </h3>
        <h3>
          Busco sempre eficincia e simplicidade na criao de projetos para que
          sua utilizao seja intuitiva, eficaz e prazerosa por parte dos
          usurios.
        </h3>
      </div>
    </div>
  );
};

export default Sobre;
